+font('Open Sans Bold', 'Open Sans Bold')
+font('Open Sans Extrabold', 'Open Sans Extrabold')
+font('Open Sans Semibold', 'Open Sans Semibold')
+font('Open Sans', 'Open Sans')


=open-b
	font-family: "Open Sans Bold", Arial, sans-serif
=open-exb
	font-family: "Open Sans Bold", Arial, sans-serif
=open-sb
	font-family: "Open Sans Semibold", Arial, sans-serif
=open
	font-family: "Open Sans", Arial, sans-serif
=br
	border-radius: 4px

=shadow($color)
	box-shadow: 0 3px 0 darken($color, 6%)

$accent: #76C7C0
$accent-dark: darken($accent, 15%)
$red: #E8645A

body
	+open
	font-size: 14px
	line-height: 1.4
	color: #000

.button
	background-color: $red
	+shadow($red)
	+br 
	padding: 15px 30px
	color: #fff
	white-space: nowrap
	transition: background-color 0.2s ease
	&:hover
		color: #fff
		text-decoration: none
		background-color: lighten($red, 4%)

h3
	font-size: 25px