@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

input[type="submit"], button { cursor: pointer; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("fonts/svgfont.eot"); src: url("fonts/svgfont.eot?#iefix") format("eot"), url("fonts/svgfont.woff") format("woff"), url("fonts/svgfont.woff2") format("woff2"), url("fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

.icon-sepa:before { content: ""; }

.icon-skrill:before { content: ""; }

.icon-badoo:before { content: ""; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; margin-top: -10px; font: 0/0 a; text-shadow: none; color: transparent; height: 20px; background: none; z-index: 1; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 30px; line-height: 30px; content: "prev"; display: inline-block; vertical-align: middle; font-size: 15px; color: #fff; background: #000; }

.slick-next { right: 0; left: auto; }
.slick-next:before { content: "next"; display: inline-block; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { background: #000; }
.slick-dots button { display: block; width: 10px; height: 10px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }

@font-face { font-family: "Open Sans Bold"; src: url("fonts/Open Sans Bold.woff2") format("woff2"), url("fonts/Open Sans Bold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Open Sans Extrabold"; src: url("fonts/Open Sans Extrabold.woff2") format("woff2"), url("fonts/Open Sans Extrabold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Open Sans Semibold"; src: url("fonts/Open Sans Semibold.woff2") format("woff2"), url("fonts/Open Sans Semibold.woff") format("woff"); font-weight: normal; font-style: normal; }
@font-face { font-family: "Open Sans"; src: url("fonts/Open Sans.woff2") format("woff2"), url("fonts/Open Sans.woff") format("woff"); font-weight: normal; font-style: normal; }
body { font-family: "Open Sans", Arial, sans-serif; font-size: 14px; line-height: 1.4; color: #000; }

.button { background-color: #E8645A; box-shadow: 0 3px 0 #e44b3f; border-radius: 4px; padding: 15px 30px; color: #fff; white-space: nowrap; transition: background-color 0.2s ease; }
.button:hover { color: #fff; text-decoration: none; background-color: #ea756c; }

h3 { font-size: 25px; }

body { background-color: moccasin; }

.header { background-color: ivory; height: 70px; margin-bottom: 20px; margin-top: 70px; z-index: 100; }
.header__in { padding-left: 20px; padding-right: 20px; padding-top: 5px; position: relative; }

.logo { display: block; position: absolute; top: -65px; left: 30px; z-index: 100; }
.logo img { height: 220px; z-index: 1000; }

.adress { font-size: 16px; position: relative; }
.adress span { position: absolute; right: 0; display: block; margin-top: 5px; }
.adress .phone { top: -45px; }
.adress .fa { color: maroon; margin-right: 5px; }

.info { position: absolute; top: -60px; left: 220px; text-align: center; color: maroon; }
.info span { display: block; margin-top: 3px; }

.award { float: right; display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; }
.award img { display: block; height: 62px; margin-left: 10px; cursor: pointer; }

.slider, .product, .news, .menu, .header { box-shadow: 0 0 8px rgba(0, 0, 0, 0.2); }

.menu { background-color: maroon; margin-bottom: 20px; padding: 20px; border-radius: 6px; }

.main-nav { display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-around; }
.main-nav a { display: block; color: #fff; font-family: "Open Sans Semibold", Arial, sans-serif; font-size: 18px; transition: all 0.2s ease; }
.main-nav a:hover { text-decoration: none; color: yellowgreen; }

.slider { background-color: #fff; padding: 20px; height: 360px; margin-bottom: 20px; z-index: 500; }
.slider__in { height: 100%; width: 100%; border: 1px solid silver; padding: 10px; background-color: ivory; }

.slide { height: 100%; }
.slide img { display: block; height: 100%; float: left; }

.slide-data { width: 360px; float: right; }
.slide-data span { display: block; font-size: 18px; font-family: "Open Sans Bold", Arial, sans-serif; margin-bottom: 15px; color: maroon; }

.arrow { position: absolute; bottom: 10px; }

.arrow1 { right: 80px; }

.arrow2 { right: 20px; }

.product { background-color: #fff; padding: 20px; }

.item { margin-top: 20px; margin-bottom: 10px; background-color: ivory; padding: 10px; border: 1px solid silver; display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; justify-content: space-between; transition: background-color 0.3s ease; }
.item:hover { background-color: #ffffa4; }
.item img { width: 300px; display: block; border: 1px solid silver; }
.item__info { width: 350px; display: -webkit-flex; display: -moz-flex; display: -ms-flex; display: -o-flex; display: flex; flex-direction: column; justify-content: space-between; }
.item__info span { display: block; }

.item-name { font-size: 21px; color: maroon; font-family: "Open Sans Bold", Arial, sans-serif; }

.news { background-color: #fff; padding: 20px; margin-bottom: 40px; }
.news__header { display: block; font-size: 18px; font-style: italic; font-family: "Open Sans Bold", Arial, sans-serif; color: maroon; margin-top: 20px; margin-bottom: 15px; }
.news img { margin-bottom: 10px; display: block; }
.news__item { padding-bottom: 20px; margin-bottom: 25px; border-bottom: 1px solid silver; }
.news p { display: block; margin-top: 32px; }

.news-data { margin-bottom: 5px; display: block; float: right; }

.more { display: block; margin-top: 15px; }

h3 .fa { color: yellowgreen; margin-right: 5px; }

.separator { height: 1px; background-color: silver; margin-bottom: 10px; margin-top: 10px; }


