// Normally mainpage styles

body
	background-color: moccasin

.header
	background-color: ivory
	height: 70px
	margin-bottom: 20px
	margin-top: 70px
	z-index: 100
	&__in
		padding-left: 20px
		padding-right: 20px
		padding-top: 5px
		position: relative

.logo
	display: block
	position: absolute
	top: -65px
	left: 30px
	z-index: 100
	img
		height: 220px
		z-index: 1000

.adress
	font-size: 16px
	position: relative
	span 
		position: absolute
		right: 0
		display: block
		margin-top: 5px
	.phone
		top: -45px
	.fa 
		color: maroon
		margin-right: 5px

.info
	position: absolute
	top: -60px
	left: 220px
	text-align: center
	color: maroon
	span
		display: block
		margin-top: 3px

.award
	float: right
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	img
		display: block
		height: 62px
		margin-left: 10px
		cursor: pointer

.slider,
.product,
.news,
.menu,
.header
	box-shadow: 0 0 8px rgba(0,0,0,.2)

.menu
	background-color: maroon
	margin-bottom: 20px
	padding: 20px
	border-radius: 6px

.main-nav
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-around
	a 
		display: block
		color: #fff
		+open-sb
		font-size: 18px
		transition: all 0.2s ease
		&:hover
			text-decoration: none
			color: yellowgreen

.slider
	background-color: #fff
	padding: 20px
	height: 360px
	margin-bottom: 20px
	z-index: 500
	&__in
		height: 100%
		width: 100%
		border: 1px solid silver
		padding: 10px
		background-color: ivory

.slide
	height: 100%
	img
		display: block
		height: 100%
		float: left

.slide-data
	width: 360px
	float: right
	span
		display: block
		font-size: 18px
		+open-b
		margin-bottom: 15px
		color: maroon

//.slick-next
//	right: 50px
//	bottom: 20px
//
//.slick-prev
//	right: 100px
//	bottom: 20px

.arrow
	position: absolute
	bottom: 10px

.arrow1
	right: 80px

.arrow2
	right: 20px

.product
	background-color: #fff
	padding: 20px

.item
	margin-top: 20px
	margin-bottom: 10px
	background-color: ivory
	padding: 10px
	border: 1px solid silver
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-between
	transition: background-color 0.3s ease
	&:hover
		background-color: darken(ivory, 15%)
	img
		width: 300px
		display: block
		border: 1px solid silver
	&__info
		width: 350px
		display: -webkit-flex
		display: -moz-flex
		display: -ms-flex
		display: -o-flex
		display: flex
		flex-direction: column
		justify-content: space-between
		span
			display: block

//.item:nth-child(even)
//	flex-direction: row-reverse

.item-name
	font-size: 21px
	color: maroon
	+open-exb


.news
	background-color: #fff
	padding: 20px
	margin-bottom: 40px
	&__header
		display: block
		font-size: 18px
		font-style: italic
		+open-b
		color: maroon
		margin-top: 20px
		margin-bottom: 15px
	img 
		margin-bottom: 10px
		display: block
	&__item
		padding-bottom: 20px
		margin-bottom: 25px
		border-bottom: 1px solid silver
	p 
		display: block
		margin-top: 32px

.news-data
	margin-bottom: 5px
	display: block
	float: right

.more
	display: block
	margin-top: 15px

h3
	.fa
		color: yellowgreen
		margin-right: 5px

.separator
	height: 1px
	background-color: silver
	margin-bottom: 10px
	margin-top: 10px